import {DataModel} from "./DataModel";

export class SearchData
{
    monitoring_urls:any[] = [];

    constructor(private model:DataModel) {}


    updateData()
    {
        if(this.model.monitoringUrls)
        {
            /*for(let i:number = 0; i < this.model.monitoringUrls.length; i++)
            {
                for(let j:number = 0; j < this.model.monitoringUrls[j].urls.length; j++)
                {
                    let url = this.model.monitoringUrls[i].urls[j];
                    url.level = i;
                    this.monitoring_urls.push()
                }
            }*/
            let s:any[] = this.generateURLS("latest");
            console.log(s);
        }
    }

    generateURLS(time:string):any[]
    {
        if(!this.model.config) return;

        let result:any[] = [];

        const remote_url:string = this.model.getRemoteURL();
        const config:any = this.model.config;

        let capture_dir:string = config.data_dir + "/capture";
        let thumbnail_dir:string = config.data_dir + "/thumbnail";
        const analysis_dir:string = config.data_dir + "/analysis";

        if (this.model.configuration.get().enableMadVision) {
            capture_dir = analysis_dir + "/madvision";
            thumbnail_dir = analysis_dir + "/madvision_thumbnail";
        }
        const plot_analysis_dir:string = analysis_dir + "/plot_analysis/latest";
        const plot_pathway_dir:string = analysis_dir + "/plot_pathway/latest";

        for (let i: number = 0; i < this.model.monitoringUrls.length; i++)
        {
            result.push({urls: []});
            for (let j: number = 0; j < this.model.monitoringUrls[i].urls.length; j++)
            {
                result[i].urls.push({});
                if ((result[i].urls[j].file_prefix == null)) {
                    result[i].urls[j].thumbnail = remote_url + "assets/img/img-missing.svg";
                    result[i].urls[j].image = remote_url + "assets/img/img-missing.svg";
                    result[i].urls[j].analysis_plot = remote_url + "assets/img/img-missing.svg";
                    result[i].urls[j].plot_pathway = remote_url + "assets/img/img-missing.svg";
                    result[i].urls[j].plot_overall_pathway = remote_url + "assets/img/img-missing.svg";
                } else {
                    result[i].urls[j].thumbnail = remote_url + thumbnail_dir + "/" + time + "/" + this.model.monitoringUrls[i].urls[j].file_prefix + ".jpg";
                    result[i].urls[j].image = remote_url + capture_dir + "/" + time + "/" + this.model.monitoringUrls[i].urls[j].file_prefix + ".jpg";
                    result[i].urls[j].plot_analysis = remote_url + plot_analysis_dir + "/" + this.model.monitoringUrls[i].urls[j].file_prefix + ".png";
                    result[i].urls[j].plot_pathway = remote_url + plot_pathway_dir + "/" + this.model.monitoringUrls[i].urls[j].file_prefix + ".png";
                    result[i].urls[j].plot_overall_pathway = remote_url + plot_pathway_dir + "/overall_pathway.png";

                    //this.setPlots(this.plot_name);
                }
            }
        }

        return result;
    }
}



